<template>
    <div class="home">
        <ul class="menu-list">
            <li class="item" v-for="item in menuFixedList" :key=" item.MENU_NO ">
                <a href="javascript:;">
                    <i :class=" 'icon icon-menu-' + item.MENU_NO "></i>
                    <cite>{{ item.MENU_NAME }}</cite>
                </a>
            </li>
        </ul>
        <ul class="menu-list menu-default-list" :class=" 'row-' + Math.ceil((menuDefaultList.length + 1) / 4) ">
            <li class="item" :class="'item-' + index" v-for="(item, index) in menuDefaultList" :key=" item.MENU_NO ">
                <a href="javascript:;">
                    <i :class=" 'icon icon-menu-' + item.MENU_NO "></i>
                    <cite>{{ item.MENU_NAME }}</cite>
                </a>
            </li>

            <li class="item">
                <a href="#/home/shortcut-setting">
                    <i class="icon icon-menu-all"></i>
                    <cite>全部</cite>
                </a>
            </li>
        </ul>

        <div class="community">
            <div class="hd">
                <div class="logo"></div>
            </div>
            <div class="product-list">
                <div class="row">
                    <template v-if=" index < 3  " v-for="(item, index) in productList " >
                        <a href="javascript:;" class="item" :class=" 'item-' + index ">
                            <img :src="item.img" alt="">
                            <cite>{{ item.title }}</cite>
                            <span>{{ item.des }}</span>
                        </a>
                    </template>
                </div>
                <div class="row">
                    <template v-if=" index >= 3  " v-for="(item, index) in productList " >
                        <a href="javascript:;" class="item" :class=" 'item-' + index ">
                            <img :src="item.img" alt="">
                            <cite>{{ item.title }}</cite>
                            <span>{{ item.des }}</span>
                        </a>
                    </template>
                </div>
            </div>
        </div>


    </div>
</template>

<script>

export default {
    data () {
        return {
            menuFixedList: [],
            menuDefaultList: [],
            productList: [
                {
                    id: 1,
                    title: '核桃芝麻粉1',
                    des: '送葛根木瓜魔芋粉',
                    img: 'static/img/temp/img-community-product-1.png',
                    sale: false
                },
                {
                    id: 2,
                    title: '农家土鸡蛋2',
                    des: '山区基地直供',
                    img: 'static/img/temp/img-community-product-2.png',
                    sale: true
                },
                {
                    id: 3,
                    title: '农家土鸡蛋3',
                    des: '山区基地直供',
                    img: 'static/img/temp/img-community-product-2.png',
                    sale: false
                },
                {
                    id: 4,
                    title: '核桃芝麻粉4',
                    des: '送葛根木瓜魔芋粉',
                    img: 'static/img/temp/img-community-product-2.png',
                    sale: false
                },
                {
                    id: 5,
                    title: '农家土鸡蛋5',
                    des: '山区基地直供',
                    img: 'static/img/temp/img-community-product-2.png',
                    sale: true
                },
                {
                    id: 6,
                    title: '核桃芝麻粉6',
                    des: '送葛根木瓜魔芋粉',
                    img: 'static/img/temp/img-community-product-1.png',
                    sale: false
                }
            ]
        };
    },
    created (){
        console.log(this.$route.meta.menuAuth);
        this.menuFixedList = this.$store.state.menuAuth.menuHomeFxied;
        this.menuDefaultList = this.$store.state.menuAuth.menuHomeDefault;
    },
    computed: {
    },
    methods: {
    },
    components: {
    }
};
</script>

<style lang="scss" scoped>
    .home
    {
        
    }
    .menu-list
    {
        @include clearfix();
        .item
        {
            float: left;
            width: 25%;
            a
            {
                position: relative;
                display: block;
                padding-top: 122px;
                height: 188px;
                text-align: center;

                .icon
                {
                    top: 40px;
                    left: 50%;
                    @include transform(translateX(-50%));
                }
                cite
                {
                    font-size: 13px;
                    color: #272727;
                }
                
            }
        }
    }

    .menu-default-list
    {
        background-color: #fff;
        .item
        {
            position: relative;

            @include mini-border(right, #F6F6F6, before);
            @include mini-border(bottom, #F6F6F6, after);
        }
        .item:nth-of-type(4n+4)
        {
            &:before
            {
                content: none;
            }
        }

        &.row-1
        {
            .item
            {
                &:after
                {
                    content: none;
                }
            }
        }

        &.row-2
        {
            .item:nth-of-type(n+5)
            {
                &:after
                {
                    content: none;
                }
            }
        }
    }
    
    .community
    {
        .hd
        {
            padding-top: 30px;
            height: 90px;
            text-align: center;

            .logo
            {
                position: relative;
                display: inline-block;
                width: 162px;
                height: 50px;
                @include image-set('../../assets/img/home/community-e-bank-logo');

                &:before,
                &:after
                {
                    content: '';
                    position: absolute;
                    top: 60%;
                    left: -100px;
                    width: 80px;
                    height: 1px;
                    background-color: #B5B5B5;
                }
                &:after
                {
                    left: auto;
                    right: -100px;
                }
            }

        }

        .product-list
        {
            .row
            {
                position: relative;
                margin: 10px 0px;
                padding: 0px 10px;
                @include clearfix();

                .item
                {
                    position: relative;
                    display: block;
                    padding-right: 155px;
                    padding-left: 20px;
                    padding-top: 26px;
                    background-color: #fff;
                    border-radius: 5px;
                    height: 180px;
                    width: 360px;

                    cite,span
                    {
                        display: block;
                        color: #272727;
                        font-size: 16px;
                        line-height: 1.5em;
                    }
                    cite
                    {
                        @include ellipsis();
                    }
                    span
                    {
                        color: #999;
                        font-size: 13px;
                    }
                    img
                    {
                        position: absolute;
                        top: 28px;
                        right: 20px;
                        max-width: 124px;
                        max-height: 124px;
                    }
                }
            }

            .row:nth-of-type(2n+1)
            {
                padding-left: 380px;
                .item:first-child
                {
                    position: absolute;
                    top: 0px;
                    left: 10px;
                    height: 370px;
                    padding: 20px;
                    text-align: center;
                    img
                    {
                        position: absolute;
                        top: auto;
                        right: 50%;
                        bottom: 24px;
                        max-width: 208px;
                        max-height: 208px;

                        @include transform(translateX(50%));
                    }
                }
                .item:last-child
                {
                    margin-top: 10px;
                }
            }
            .row:nth-of-type(2n+2)
            {
                padding-right: 380px;
                .item:last-child
                {
                    position: absolute;
                    top: 0px;
                    right: 10px;
                    height: 370px;
                    padding: 20px;
                    text-align: center;
                    img
                    {
                        position: absolute;
                        top: auto;
                        right: 50%;
                        bottom: 24px;
                        max-width: 208px;
                        max-height: 208px;

                        @include transform(translateX(50%));
                    }
                }
                .item:first-child
                {
                    margin-bottom: 10px;
                }
            }
        }
    }

</style>





